import React from 'react';
import {
  Table,
  TableHead,
  TableRow,
  Checkbox,
  TableCell,
  Tooltip,
  IconButton,
  TableBody,
  FormControlLabel,
  TableContainer,
  Box,
  TablePagination,
} from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';
import {
  MoreVert as IconMoreVert,
  PlayCircleOutline as IconPlayCircleOutline,
  Link as IconLink,
} from '@material-ui/icons';

const OrangeCheckbox = withStyles((theme) => ({
  root: {
    color: theme.palette.primary.light,
    '&$checked': {
      color: theme.palette.primary.main,
    },
  },
  checked: {},
}))((props) => <Checkbox color="default" {...props} />);

const CustomTable = ({ tableDate }) => {
  const [page, setPage] = React.useState(0);
  const [rowsPerPage, setRowsPerPage] = React.useState(5);

  const handleChangePage = (event, newPage) => {
    console.log(newPage);
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    console.log('1111');
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };
  return (
    <>
      <TableContainer sx={{ maxHeight: 440 }}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell style={{ textAlign: 'center' }} width={'10%'}>
                <FormControlLabel control={<OrangeCheckbox />} />
              </TableCell>
              <TableCell style={{ textAlign: 'center' }} width={'15%'}>
                PATIENT
              </TableCell>
              <TableCell style={{ textAlign: 'center' }} width={'30%'}>
                INHALT
              </TableCell>
              <TableCell style={{ textAlign: 'center' }} width={'15%'}>
                AKTION
              </TableCell>
              <TableCell style={{ textAlign: 'center' }} width={'15%'}>
                RECORDING
              </TableCell>
              <TableCell style={{ textAlign: 'right' }} width={'15%'}>
                <Tooltip title="more">
                  <IconButton>
                    <IconMoreVert />
                  </IconButton>
                </Tooltip>
              </TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {tableDate &&
              tableDate.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((row, index) => (
                <TableRow key={index}>
                  <TableCell style={{ textAlign: 'center' }}>
                    <FormControlLabel control={<OrangeCheckbox />} />
                  </TableCell>

                  <TableCell style={{ textAlign: 'center' }}>
                    <Box
                      display={'flex'}
                      flexDirection="column"
                      justifyContent={'center'}
                      style={{ textAlign: 'center' }}
                    >
                      <h3 style={{ fontWeight: 700, fontSize: '16px' }}>{row.user.name}</h3>
                      <h4>{row.user.phoneNumber}</h4>
                    </Box>
                  </TableCell>
                  <TableCell style={{ textAlign: 'center' }}>{row.content}</TableCell>
                  <TableCell style={{ textAlign: 'center' }}>
                    <Box display={'fex'} justifyContent="center" flexDirection={'column'}>
                      <Box textAlign="center" alignItems={'center'} display="flex" justifyContent={'center'}>
                        <p
                          style={{
                            backgroundColor: row.action.state ? '#ccffcc' : '#ffe0e0',
                            color: row.action.state ? '#118311' : '#ce8604',
                            borderRadius: '8px',
                            padding: '4px',
                          }}
                        >
                          {row.action.state ? '● OK' : '● Offten'}
                        </p>
                      </Box>
                      <Box>{row.action.data}</Box>
                    </Box>
                  </TableCell>
                  <TableCell style={{ textAlign: 'center' }}>
                    <Tooltip title="recording">
                      <IconButton>
                        <IconPlayCircleOutline />
                      </IconButton>
                    </Tooltip>
                  </TableCell>
                  <TableCell>
                    <Box display={'flex'} justifyContent={'right'} alignItems="center">
                      <span style={{ marginRight: '12px' }}>{'PVS'}</span>
                      <IconLink></IconLink>
                      <Tooltip title="more">
                        <IconButton>
                          <IconMoreVert />
                        </IconButton>
                      </Tooltip>
                    </Box>
                  </TableCell>
                </TableRow>
              ))}
          </TableBody>
        </Table>
      </TableContainer>
      <TablePagination
        rowsPerPageOptions={[5, 10, 25]}
        component="div"
        count={tableDate.length}
        rowsPerPage={rowsPerPage}
        page={page}
        onChangePage={handleChangePage}
        onChangeRowsPerPage={handleChangeRowsPerPage}
        labelRowsPerPage="Zeilen pro Seite"
      />
    </>
  );
};

export default CustomTable;
