import { USER_LOGIN } from './type';

const initialState = { isAuthenticated: false, auth: { _id: '', fullName: '' }, token: '' };

const authReducer = (state = initialState, action) => {
  switch (action.type) {
    case USER_LOGIN:
      return { ...state, isAuthenticated: true, auth: action.payload, token: action.payload.access_tokens };

    default:
      return state;
  }
};

export default authReducer;
