import React from 'react';
import { Link } from 'react-router-dom';
import PropTypes from 'prop-types';
import useStylesEvent from './stylesEvent';
import { Paper, Typography, Button, Tooltip, IconButton, Box } from '@material-ui/core';
import { Edit as IconEdit, Delete as IconDelete, Settings as IconSettings } from '@material-ui/icons';
import { withTheme } from '@material-ui/core/styles';

import ConfirmDialog from '../Confirm/ConfirmDialog';
import { deleteSchedularEvent } from '../../actions/user';
function Event({
  duration,
  meetingName,
  url,
  setOpen,
  setName,
  setDuration,
  appointmentTypes,
  id,
  sub,
  subscribe,
  setEditId,
  renderNewMeeting,
}) {
  const classes = useStylesEvent();
  const [openDialog, setOpenDialog] = React.useState(false);
  let color = null;

  switch (duration) {
    case 15:
      color = 'darkmagenta';
      break;
    case 30:
      color = 'olivedrab';
      break;
    case 60:
      color = 'darkorange';
      break;
    default:
      color = 'crimson';
  }

  const stripeCSS = {
    borderTop: `8px solid ${color}`,
    borderRadius: '1rem',
    margin: '0 -2rem',
  };
  const handleDelete = () => {
    deleteSchedularEvent(sub, id)
      .then((res) => {
        renderNewMeeting(res.data.docs);
        setOpenDialog(false);
      })
      .catch((err) => console.log(err));
  };

  return (
    <Paper elevation={6} className={classes.eventsContainer}>
      <div style={stripeCSS}></div>
      <Box display={'flex'} justifyContent="flex-end" mr={-4}>
        <Tooltip title="edit" aria-label="edit">
          <IconButton
            aria-label="edit"
            color="primary"
            onClick={(e) => {
              setEditId(id);

              setName(appointmentTypes.filter((type) => type.title === meetingName)[0]._id);
              setDuration(parseInt(duration));
              setOpen(true);
            }}
            style={{ padding: '8px' }}
            disabled={subscribe === false ? true : false}
          >
            <IconEdit />
          </IconButton>
        </Tooltip>
        <Tooltip title="delete" aria-label="delete">
          <IconButton
            aria-label="delete"
            onClick={(e) => {
              setOpenDialog(true);
              setEditId(id);
            }}
            style={{ padding: '8px' }}
          >
            <IconDelete />
          </IconButton>
        </Tooltip>
      </Box>
      <div className={classes.eventDesc}>
        <h3>{meetingName}</h3>
        <Typography variant="subtitle1">One-on-one</Typography>
      </div>
      <div className={classes.linksContainer}>
        <div>
          {/* target=_blank opens route in new tab */}
          <Link
            target="_blank"
            to={{
              pathname: `/${url}/${id}/${duration}`,
            }}
          >
            {duration} min
          </Link>
        </div>
        <Button className={classes.linkButton} variant="outlined">
          Copy Link
        </Button>
      </div>
      <ConfirmDialog
        openDialog={openDialog}
        onOK={handleDelete}
        onCancel={(e) => setOpenDialog(false)}
        title={'Confirm Dialog'}
        content={`Would you sure delete this EventSchedule?`}
      />
    </Paper>
  );
}

Event.propTypes = {
  meetingName: PropTypes.string.isRequired,
  duration: PropTypes.number.isRequired,
};

export default withTheme(Event);
