import {
  READ_APPOINTMENT_TYPES,
  UPDATE_APPOINTMENT_TYPES,
  DELETE_APPOINTMENT_TYPE,
  CREATE_APPOINTMENT_TYPE,
} from './type';

const initialState = [{ _id: '', title: '' }];

const appointmentTypeReducer = (state = initialState, action) => {
  switch (action.type) {
    case CREATE_APPOINTMENT_TYPE:
      return [...state, action.payload];
    case READ_APPOINTMENT_TYPES:
      return (state = action.payload);
    case UPDATE_APPOINTMENT_TYPES:
      return (state = state.map((type) => (type._id === action.payload._id ? action.payload : type)));
    case DELETE_APPOINTMENT_TYPE:
      return (state = state.filter((type) => type._id !== action.payload));
    default:
      return state;
  }
};

export default appointmentTypeReducer;
