import axios from 'axios';
import { appointment_type_url, get_appointment_url } from './constant';
import auth from '../auth';

const getConfig = () => {
  var config = {
    headers: { 'x-access-token': auth.getToken() },
  };
  return config;
};

export const getAppointmentFunc = (user, timezone) => {
  const config = getConfig();

  return axios
    .get(`${get_appointment_url}/${user}?timezone=${timezone}`, config)
    .then((response) => {
      return response;
    })
    .catch((err) => {
      return { error: err.response.data };
    });
};

export const submitAppointmentFunc = (sendInfos) => {
  const config = getConfig();

  return axios
    .post(get_appointment_url, sendInfos, config)
    .then((response) => {
      return response;
    })
    .catch((err) => {
      return { error: err.response.data };
    });
};

export const deleteAppointmentFunc = (id) => {
  const config = getConfig();

  return axios
    .delete(`${get_appointment_url}/${id}`, config)
    .then((response) => {
      return response;
    })
    .catch((err) => {
      return { error: err.response.data };
    });
};
export const readAppointmentType = (subscribe) => {
  return axios
    .get(`${appointment_type_url}/?subscribe=${subscribe}`)
    .then((res) => {
      return res;
    })
    .catch((err) => {
      return { error: err.response.data };
    });
};
export const createAppointmentType = (type) => {
  return axios
    .post(`${appointment_type_url}`, type)
    .then((res) => {
      return res;
    })
    .catch((err) => {
      return { error: err.response.data };
    });
};

export const updateAppointmentType = (type) => {
  return axios
    .put(`${appointment_type_url}/${type._id}`, type)
    .then((res) => {
      return res;
    })
    .catch((err) => {
      return { error: err.response.data };
    });
};
export const deleteAppointmentType = (id) => {
  return axios
    .delete(`${appointment_type_url}/${id}`)
    .then((res) => {
      return res;
    })
    .catch((err) => {
      return { error: err.response.data };
    });
};
