import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { useDispatch } from 'react-redux';
import { toast } from 'react-toastify';
import { useHistory, Link } from 'react-router-dom';

import { withStyles } from '@material-ui/core/styles';
import { FaUserAlt, FaLock, FaRegEnvelope, FaPhone } from 'react-icons/fa';
import { Paper, Box, Divider, TextField, InputAdornment } from '@material-ui/core';
import { Visibility as IconVisiblility, VisibilityOff as IconVisibilityOff } from '@material-ui/icons';

import auth from '../../auth';
import stylesForm from './stylesForm';
import { USER_LOGIN } from '../../store/type';
import AuthButton from '../AuthButton/AuthButton';
import { userRegFunc, userLoginFunc } from '../../actions/user';

const Form = ({ classes, type1 }) => {
  const dispatch = useDispatch();
  const [visibility, setVisibility] = React.useState(false);
  const [name, setName] = useState();
  const [email, setEmail] = useState();
  const [phone, setPhone] = useState();
  const [password, setPassword] = useState();
  const [confirm, setConfirm] = useState();
  const [formType, setformType] = useState('');
  const isLoginForm = formType === 'login' ? true : false;

  let history = useHistory();

  useEffect(() => {
    if (type1 === 'login') setformType('login');
  }, []);

  const changeFormType = () => {
    formType === 'login' ? setformType('signup') : setformType('login');
  };

  const handleSignIn = () => {
    const loginUserInfo = {
      email: email,
      password: password,
    };
    userLoginFunc(loginUserInfo)
      .then((res) => {
        if (res.status === 201) {
          auth.login(() => {
            if (res.status === 201) {
              auth.sub = res.data._id;
              auth.email = res.data.email;
              auth.phone = res.data.phone;
              auth.name = res.data.fullName;
              auth.token = res.data.access_token;
              dispatch({ type: USER_LOGIN, payload: res.data });
              history.push('/dashboard');
            } else {
            }
          });
        } else {
          toast.error('Email or Password Error!');
        }
      })
      .catch((err) => toast.error('Email or Password Error!'));
  };

  const handleSignUp = () => {
    if (confirm === password && !(confirm === '' && password === '' && name === '' && email === '')) {
      const userInfos = {
        name: name,
        email: email,
        phone: phone,
        password: password,
      };
      userRegFunc(userInfos).then((res) => {
        auth.login(() => {
          if (res.status === 201) {
            auth.sub = res.data._id;
            auth.phone = res.data.phone;
            auth.email = res.data.email;
            auth.name = res.data.fullName;
            auth.token = res.data.access_token;
            dispatch({ type: USER_LOGIN, payload: res.data });
            history.push('/profile_settings');
          } else {
            toast.error(res.error);
          }
        });
      });
    } else {
      toast.error('Please enter user information correctly!');
    }
  };

  const loginText = {
    header: 'Ins Konto einloggen',
    helpText: 'Sie haben kein Konto?',
    redirectText: 'Sign Up',
    redirectPath: '/signup',
    handleClick: handleSignIn,
  };

  const signupText = {
    header: 'Einen Account registrieren',
    helpText: 'Sie haben bereits ein Konto? ',
    redirectText: 'Login',
    redirectPath: '/login',
    handleClick: handleSignUp,
  };

  return (
    <Paper elevation={6} className={classes.paper}>
      <h2 className={classes.loginHeader}>{isLoginForm ? loginText.header : signupText.header}</h2>
      {formType === 'login' ? (
        <>
          <TextField
            name="email"
            label="E-mail"
            type="text"
            variant="outlined"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <FaRegEnvelope />
                </InputAdornment>
              ),
            }}
          />

          <TextField
            name="password"
            label="Passwort"
            variant="outlined"
            margin="normal"
            type={visibility === true ? 'text' : 'password'}
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <FaLock />
                </InputAdornment>
              ),
              endAdornment: (
                <InputAdornment style={{ padding: 0 }} position="start" component="div">
                  {visibility === true ? (
                    <div
                      style={{ cursor: 'pointer' }}
                      onClick={(e) => {
                        setVisibility(false);
                      }}
                      className={classes.visiblediv}
                    >
                      <IconVisibilityOff style={{ padding: 0 }} />
                    </div>
                  ) : (
                    <div style={{ cursor: 'pointer' }} onClick={(e) => setVisibility(true)}>
                      <IconVisiblility />
                    </div>
                  )}
                </InputAdornment>
              ),
            }}
          />
        </>
      ) : (
        <>
          <TextField
            name="fullname"
            label="Name"
            type="text"
            variant="outlined"
            value={name}
            size="small"
            margin="normal"
            onChange={(e) => setName(e.target.value)}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <FaUserAlt />
                </InputAdornment>
              ),
            }}
          />
          <TextField
            name="email"
            label="E-mail"
            type="text"
            variant="outlined"
            value={email}
            size="small"
            margin="normal"
            onChange={(e) => setEmail(e.target.value)}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <FaRegEnvelope />
                </InputAdornment>
              ),
            }}
          />
          <TextField
            name="phone"
            label="Phone Number"
            type="number"
            variant="outlined"
            value={phone}
            size="small"
            margin="normal"
            onChange={(e) => setPhone(e.target.value)}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <FaPhone />
                </InputAdornment>
              ),
            }}
          />
          <TextField
            name="password"
            label="Passwort"
            type={visibility === true ? 'text' : 'password'}
            variant="outlined"
            value={password}
            size="small"
            autoFocus
            margin="normal"
            onChange={(e) => setPassword(e.target.value)}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <FaLock />
                </InputAdornment>
              ),
              endAdornment: (
                <InputAdornment className={classes.adornment} position="start" component="div">
                  {visibility === true ? (
                    <div
                      style={{ cursor: 'pointer' }}
                      onClick={(e) => {
                        setVisibility(false);
                      }}
                    >
                      <IconVisibilityOff />
                    </div>
                  ) : (
                    <div style={{ cursor: 'pointer' }} onClick={(e) => setVisibility(true)}>
                      <IconVisiblility />
                    </div>
                  )}
                </InputAdornment>
              ),
            }}
          />
          <TextField
            name="confirm"
            label="Bestätigen Passwort"
            type={visibility === true ? 'text' : 'password'}
            variant="outlined"
            value={confirm}
            autoFocus
            size="small"
            margin="normal"
            onChange={(e) => setConfirm(e.target.value)}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <FaLock />
                </InputAdornment>
              ),
              endAdornment: (
                <InputAdornment className={classes.adornment} position="start" component="div">
                  {visibility === true ? (
                    <div
                      style={{ cursor: 'pointer' }}
                      onClick={(e) => {
                        setVisibility(false);
                      }}
                    >
                      <IconVisibilityOff />
                    </div>
                  ) : (
                    <div style={{ cursor: 'pointer' }} onClick={(e) => setVisibility(true)}>
                      <IconVisiblility />
                    </div>
                  )}
                </InputAdornment>
              ),
            }}
          />
        </>
      )}

      <AuthButton buttonType={type1} click={isLoginForm ? loginText.handleClick : signupText.handleClick} />
      <Box style={{ marginTop: 10 }}>
        <Divider />
        <Box className={classes.helpText}>
          {isLoginForm ? loginText.helpText : signupText.helpText}
          <Link to={formType === 'login' ? loginText.redirectPath : signupText.redirectPath} onClick={changeFormType}>
            {isLoginForm ? loginText.redirectText : signupText.redirectText}
          </Link>
        </Box>
      </Box>
    </Paper>
  );
};

Form.propTypes = {
  classes: PropTypes.object.isRequired,
  type1: PropTypes.string.isRequired,
};

export default withStyles(stylesForm)(Form);
