import React, { useRef } from 'react';
import { toast } from 'react-toastify';
import { useDispatch } from 'react-redux';
import { useParams, useHistory } from 'react-router-dom';
import moment from 'moment';
import {
  Button,
  Box,
  InputAdornment,
  FormControlLabel,
  Checkbox,
  FormGroup,
  s,
  Avatar,
  IconButton,
} from '@material-ui/core';
import {
  MailOutline as IconMailOutline,
  AccountCircle as IconAccountCircle,
  Lock as IconLock,
  Visibility as IconVisiblility,
  VisibilityOff as IconVisibilityOff,
  Alarm as IconAlarm,
  Phone as IconPhone,
} from '@material-ui/icons';

import { withStyles } from '@material-ui/core/styles';
import { TextValidator, ValidatorForm } from 'react-material-ui-form-validator';
import { Autocomplete } from '@material-ui/lab';

import { USER_LOGIN } from '../../store/type';
import { getUserByIdFunc, updateUserProfile } from '../../actions/user';

import useStyleProfile from './styleProfile';
import Navbar from '../../Components/Navbar/Navbar';
import ConfirmDialog from '../../Components/Confirm/ConfirmDialog';

const Profile = () => {
  const ref = useRef();
  const dispatch = useDispatch();
  const classes = useStyleProfile();
  const history = useHistory();
  const params = useParams();
  const initialProfile = {
    email: '',
    fullName: '',
    timezone: '',
    url: '',
    password: '',
    phone: '',

    availability: { days: {}, hours: { start: null, end: null } },
  };
  const [profile, setProfile] = React.useState({});
  const [visibility, setVisibility] = React.useState(false);
  const [hours, setHours] = React.useState({});
  const [workDays, setWorkDays] = React.useState({});
  const [newPassword, setNewPassword] = React.useState('');
  const [newConfirm, setNewConfirm] = React.useState('');
  const [openDialog, setOpenDialog] = React.useState(false);
  const handleChange = (e) => {
    setProfile({ ...profile, [e.target.name]: e.target.value });
  };

  const handleDays = (e) => {
    setWorkDays({ ...workDays, [e.target.name]: e.target.checked });
  };

  const handleUpdateProfile = () => {
    setProfile({ ...profile, availability: { hours: hours, days: workDays } });
    setProfile({ ...profile, password: newPassword });
    updateUserProfile(profile)
      .then((res) => {
        toast.success('Your profile has been successfully updated.');
        dispatch({ type: USER_LOGIN, payload: res.data.doc });
      })
      .catch((err) => console.log(err));
  };
  React.useEffect(() => {
    getUserByIdFunc(params.id)
      .then((res) => {
        setProfile(res.data);
        setWorkDays(res.data.availability.days);
        setHours(res.data.availability.hours);
      })
      .catch((err) => {
        setProfile(initialProfile);
      });
    ValidatorForm.removeValidationRule('isPasswordMatch');
  }, []);

  React.useEffect(() => {
    ValidatorForm.addValidationRule('isPasswordMatch', (value) => {
      if (value !== newPassword) {
        return false;
      }
      return true;
    });
  }, [newConfirm]);

  const OrangeCheckbox = withStyles((theme) => ({
    root: {
      color: theme.palette.primary.light,
      '&$checked': {
        color: theme.palette.primary.main,
      },
    },
    checked: {},
  }))((props) => <Checkbox color="default" {...props} />);

  const renderCheckBoxes = (days) => {
    return Object.keys(days).map((day) => {
      return (
        <FormControlLabel
          id={day + '-checkbox-field'}
          classes={{ root: classes.checkBoxLabel, label: classes.checkBoxLabelLabel }}
          key={day}
          control={<OrangeCheckbox checked={days[day]} name={day} onChange={handleDays} />}
          label={day + 's'}
          labelPlacement="bottom"
        />
      );
    });
  };
  return (
    <Box className={classes.container}>
      <Navbar picture={profile && profile.picture} name={profile && profile.fullName} />
      <ValidatorForm
        ref={ref}
        onSubmit={() => {
          setOpenDialog(true);
          console.log('---');
        }}
      >
        <Box className={classes.subHeader}>
          <Box>
            <Box mb={4} display="flex" justifyContent={'center'}>
              <Avatar style={{ width: '100px', height: '100px' }} />
            </Box>

            <Box mb={4}>
              <TextValidator
                fullWidth
                label={'FullName'}
                name="fullName"
                variant={'outlined'}
                value={profile.fullName}
                type="text"
                validators={['required']}
                errorMessages={['FullName field is required']}
                onChange={handleChange}
                InputProps={{
                  startAdornment: (
                    <InputAdornment className={classes.adornment} position="start" component="div">
                      <IconAccountCircle />
                    </InputAdornment>
                  ),
                }}
              />
            </Box>
            <Box mb={4}>
              <TextValidator
                fullWidth
                label="Email"
                name="email"
                variant={'outlined'}
                value={profile.email}
                type="email"
                validators={['required', 'isEmail']}
                errorMessages={['Email field is required', 'email is not valid']}
                onChange={handleChange}
                InputProps={{
                  startAdornment: (
                    <InputAdornment className={classes.adornment} position="start" component="div">
                      <IconMailOutline />
                    </InputAdornment>
                  ),
                }}
              />
            </Box>
            <Box mb={4}>
              <TextValidator
                fullWidth
                label="Phone Number"
                name="phone"
                variant={'outlined'}
                value={profile.phone}
                type="number"
                validators={['required']}
                errorMessages={['Email field is required']}
                onChange={handleChange}
                InputProps={{
                  startAdornment: (
                    <InputAdornment className={classes.adornment} position="start" component="div">
                      <IconPhone />
                    </InputAdornment>
                  ),
                }}
              />
            </Box>
          </Box>
          <Box mb={4}>
            <TextValidator
              // className={classes.urlField}
              fullWidth
              id="url-field"
              label="URL"
              variant="outlined"
              value={profile && profile.url}
              validators={['required']}
              errorMessages={['URL field is required']}
              onChange={handleChange}
              // onChange={(e) => setUrl(e.target.value)}
              InputProps={{
                startAdornment: (
                  <InputAdornment className={classes.adornment} position="start" component="div">
                    http://dentoconnect.herokuapp.com/
                  </InputAdornment>
                ),
              }}
            />
          </Box>
          <Box mb={4}>
            <Autocomplete
              id="timezone-field"
              options={moment.tz.names()}
              getOptionLabel={(option) => option + ' (GMT' + moment.tz(option).format('Z') + ')'}
              inputValue={profile.timezone}
              value={profile.timezone}
              onChange={(e, v) => {
                setProfile({ ...profile, timezone: v });
              }}
              onInputChange={(event, newInputValue) => {
                setProfile({ ...profile, timezone: newInputValue });
              }}
              renderInput={(params) => (
                <TextValidator
                  {...params}
                  value={profile.timezone}
                  label="Type a city or zone"
                  variant="outlined"
                  validators={['required']}
                  errorMessages={['TImeZone field is required']}
                />
              )}
            />
          </Box>
          <Box mb={4}>
            <Box className={classes.hoursRow}>
              <TextValidator
                fullWidth
                id="start-hours-field"
                value={hours.start}
                variant="outlined"
                type="time"
                label="From"
                name="start"
                validators={['required']}
                errorMessages={['StartTIme field is required']}
                onChange={(e) => {
                  setHours({ ...hours, start: e.target.value });
                }}
                InputProps={{
                  startAdornment: (
                    <InputAdornment className={classes.adornment} position="start" component="div">
                      <IconAlarm />
                    </InputAdornment>
                  ),
                }}
                // onChange={handleHours}
              />
              <span>-</span>
              <TextValidator
                id="end-hours-field"
                fullWidth
                value={hours.end}
                variant="outlined"
                type="time"
                label="To"
                name="end"
                onChange={(e) => {
                  setHours({ ...hours, end: e.target.value });
                }}
                validators={['required']}
                errorMessages={['EndTime field is required']}
                InputProps={{
                  startAdornment: (
                    <InputAdornment className={classes.adornment} position="start" component="div">
                      <IconAlarm />
                    </InputAdornment>
                  ),
                }}
                // onChange={handleHours}
              />
            </Box>
          </Box>
          <Box mb={4}>
            <div>Available Days: </div>
            <FormGroup row className={classes.checkBoxGroup}>
              {renderCheckBoxes(workDays && workDays)}
            </FormGroup>
          </Box>
          <Box mb={4}>
            <TextValidator
              fullWidth
              name="password"
              label="New Password"
              value={newPassword}
              type={visibility === true ? 'text' : 'password'}
              variant="outlined"
              onChange={(e) => setNewPassword(e.target.value)}
              InputProps={{
                startAdornment: (
                  <InputAdornment className={classes.adornment} position="start" component="div">
                    <IconLock />
                  </InputAdornment>
                ),
                endAdornment: (
                  <InputAdornment className={classes.adornment} position="start" component="div">
                    {visibility === true ? (
                      <IconButton
                        onClick={(e) => {
                          setVisibility(false);
                        }}
                      >
                        <IconVisibilityOff />
                      </IconButton>
                    ) : (
                      <IconButton onClick={(e) => setVisibility(true)}>
                        <IconVisiblility />
                      </IconButton>
                    )}
                  </InputAdornment>
                ),
              }}
            />
          </Box>
          <Box mb={4}>
            <TextValidator
              fullWidth
              name="newConfirm"
              value={newConfirm}
              onChange={(e) => {
                setNewConfirm(e.target.value);
              }}
              type={visibility === true ? 'text' : 'password'}
              label="Confirm New Password"
              variant="outlined"
              validators={['isPasswordMatch']}
              errorMessages={['password mismatch']}
              InputProps={{
                startAdornment: (
                  <InputAdornment className={classes.adornment} position="start" component="div">
                    <IconLock />
                  </InputAdornment>
                ),
                endAdornment: (
                  <InputAdornment className={classes.adornment} position="start" component="div">
                    {visibility === true ? (
                      <IconButton
                        onClick={(e) => {
                          setVisibility(false);
                        }}
                      >
                        <IconVisibilityOff />
                      </IconButton>
                    ) : (
                      <IconButton onClick={(e) => setVisibility(true)}>
                        <IconVisiblility />
                      </IconButton>
                    )}
                  </InputAdornment>
                ),
              }}
            />
          </Box>
          <Box display={'flex'} justifyContent="flex-end">
            <Box mr={4}>
              <Button color="primary" variant="outlined" type="submit">
                Save
              </Button>
            </Box>
            <Box>
              <Button
                color="primary"
                variant="outlined"
                onClick={(e) => {
                  history.push('/dashboard');
                }}
              >
                Calcel
              </Button>
            </Box>
          </Box>
        </Box>
        <ConfirmDialog
          onOK={handleUpdateProfile}
          onCancel={(e) => setOpenDialog(false)}
          title={'Confirm Dialog'}
          content={`Would you sure delete this EventSchedule?`}
          openDialog={openDialog}
        />
      </ValidatorForm>
    </Box>
  );
};

export default Profile;
