import React, { useState, useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { withStyles } from '@material-ui/core/styles';
import {
  Box,
  Button,
  Grid,
  IconButton,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  TextField,
  Tooltip,
  Checkbox,
  FormControlLabel,
} from '@material-ui/core';
import {
  Delete as IconDelete,
  Edit as IconEdit,
  Add as IconAdd,
  Save as IconSave,
  Clear as IconClear,
} from '@material-ui/icons';

import {
  createAppointmentType,
  deleteAppointmentType,
  readAppointmentType,
  updateAppointmentType,
} from '../../actions/appointment';

import ConfirmDialog from '../Confirm/ConfirmDialog';
import './AppointmentType.css';
import {
  READ_APPOINTMENT_TYPES,
  UPDATE_APPOINTMENT_TYPES,
  DELETE_APPOINTMENT_TYPE,
  CREATE_APPOINTMENT_TYPE,
} from '../../store/type';
import { toast } from 'react-toastify';

const styles = (theme) => ({
  paper: {
    width: '100%',
    minHeight: '400px',
    marginTop: '50px',
    marginBottom: '100px',
    padding: '10px',
  },
});

const OrangeCheckbox = withStyles((theme) => ({
  root: {
    color: theme.palette.primary.light,
    '&$checked': {
      color: theme.palette.primary.main,
    },
  },
  checked: {},
}))((props) => <Checkbox color="default" {...props} />);
const AppointmentType = ({ classes }) => {
  // const classes = useStyles();
  const dispatch = useDispatch();
  const initialType = { _id: '', title: '', subscribe: false };
  const [openDialog, setOepnDialog] = React.useState(false);
  const [newType, setNewType] = useState(initialType);
  const [types, setTypes] = useState([]);
  const handleChange = (e) => {
    setNewType({ ...newType, [e.target.name]: e.target.value });
  };
  const handleCreate = () => {
    createAppointmentType({ title: newType.title, subscribe: newType.subscribe })
      .then((res) => {
        if (res.status === 201) {
          setTypes([...types, res.data.doc]);
          dispatch({ type: CREATE_APPOINTMENT_TYPE, payload: res.data.doc });
          setNewType(initialType);
        } else {
          toast.error('AppointmentType Duplicated!');
        }
      })
      .catch((err) => {
        console.log(err + 'AddAppointment');
      });
  };
  const handleDelete = (_id) => {
    deleteAppointmentType(_id)
      .then((res) => {
        setTypes(types.filter((type) => type._id !== _id));
        dispatch({ type: DELETE_APPOINTMENT_TYPE, payload: _id });
        setOepnDialog(false);
      })
      .catch((err) => console.log(err));
  };
  const handleSave = (upadteType) => {
    updateAppointmentType(upadteType)
      .then((res) => {
        setTypes(types.map((type) => (type._id === upadteType._id ? res.data.doc : type)));
        setNewType(initialType);
        setOepnDialog(false);
        dispatch({ type: UPDATE_APPOINTMENT_TYPES, payload: res.data.doc });
      })
      .catch((err) => console.log(err));
  };
  useEffect(() => {
    readAppointmentType('a')
      .then((res) => {
        setTypes(res.data.docs);
        dispatch({ type: READ_APPOINTMENT_TYPES, payload: res.data.docs });
      })
      .catch((err) => {
        console.log(err + 'ReadAppointment');
      });
  }, []);
  return (
    <Paper elevation={6} className={classes.paper}>
      <Grid container spacing={2}>
        <Grid item md={4} sm={12} xs={12}>
          <Box display={'flex'} justifyContent={'center'} flexDirection={'column'} height="100%">
            <Box height="100%" style={{ border: '1px solid', padding: 25, borderRadius: '5px' }}>
              <Box pb={1}>AppointmentType:</Box>
              <Box>
                <TextField
                  pl={2}
                  pr={2}
                  value={newType.title}
                  variant="outlined"
                  fullWidth
                  type="text"
                  name="title"
                  onChange={handleChange}
                />
              </Box>
              <Box>
                <FormControlLabel
                  classes={{ root: classes.checkBoxLabel, label: classes.checkBoxLabelLabel }}
                  control={
                    <OrangeCheckbox
                      checked={newType.subscribe}
                      onChange={(e) => {
                        setNewType({ ...newType, subscribe: newType.subscribe === true ? false : true });
                      }}
                      name={'subscribe'}
                    />
                  }
                  label={'Subscribe'}
                  // labelPlacement="bottom"
                />
              </Box>

              <Box mt={2} display={'flex'} justifyContent={'flex-end'}>
                {newType._id ? (
                  <Box display="flex" justifyContent="space-between" width="70%">
                    <Button
                      variant="contained"
                      color="primary"
                      startIcon={<IconSave />}
                      onClick={(e) => setOepnDialog(true)}
                    >
                      Save
                    </Button>
                    <Button
                      variant="contained"
                      pl={2}
                      color="secondary"
                      startIcon={<IconClear />}
                      onClick={(e) => setNewType(initialType)}
                    >
                      Cancel
                    </Button>
                  </Box>
                ) : (
                  <Button
                    variant="outlined"
                    color="primary"
                    disabled={newType.title ? false : true}
                    startIcon={<IconAdd />}
                    onClick={handleCreate}
                  >
                    Add
                  </Button>
                )}
              </Box>
            </Box>
          </Box>
        </Grid>
        <Grid item md={8} sm={12} xs={12}>
          <Box style={{ border: '1px solid', padding: 25, borderRadius: '5px' }}>
            <Table>
              <TableHead>
                <TableRow>
                  <TableCell align="center" width={'15%'}>
                    No
                  </TableCell>
                  <TableCell align="center" width={'55%'}>
                    AppointmentType
                  </TableCell>
                  <TableCell align="center" width={'30%'}>
                    Actions
                  </TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {types.map((type, index) => (
                  <TableRow key={index}>
                    <TableCell align="center" width={'15%'}>
                      {index + 1}
                    </TableCell>
                    <TableCell align="center">{type.title}</TableCell>
                    <TableCell align="center" p={{ md: 16, sm: 8, xs: 2 }}>
                      <Tooltip title="edit" aria-label="edit">
                        <IconButton aria-label="edit" color="primary" onClick={(e) => setNewType(type)}>
                          <IconEdit />
                        </IconButton>
                      </Tooltip>
                      <Tooltip title="delete" aria-label="delete">
                        <IconButton aria-label="delete" onClick={(e) => setOepnDialog(true)}>
                          <IconDelete />
                        </IconButton>
                      </Tooltip>
                    </TableCell>
                    <ConfirmDialog
                      title={'Confirm Dialog'}
                      content={`Would you sure ${newType._id ? 'save' : 'delete'} this AppointmentType?`}
                      openDialog={openDialog}
                      onOK={(e) => {
                        newType._id ? handleSave(newType) : handleDelete(type._id);
                      }}
                      onCancel={(e) => {
                        setOepnDialog(false);
                      }}
                    ></ConfirmDialog>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </Box>
        </Grid>
      </Grid>
    </Paper>
  );
};

export default withStyles(styles)(AppointmentType);
