import { makeStyles } from '@material-ui/core/styles';

const useStyleProfile = makeStyles((theme) => ({
  container: {
    height: '100vh',
    display: 'flex',
    flexDirection: 'column',
  },
  subHeader: {
    padding: '3rem 30 0 30rem',
    flex: '0 1 auto',
  },
  checkBoxGroup: {
    border: '1px solid #ccc',
    borderRadius: '5px',
    '& label:last-child': {
      border: 'none',
    },
    justifyContent: 'center',
  },
  hoursRow: {
    display: 'flex',
    flexDirection: 'row',
    '& span': {
      fontSize: '1rem',
      padding: ' 0 5% 0 5%',
      alignSelf: 'center',
    },
  },
  checkBoxLabel: {
    borderRight: '1px solid #eee',
    margin: '0px',
    padding: '0.2rem 0 0.5rem 0',
    flex: '0 0 14.28%',
  },
  checkBoxLabelLabel: {
    fontSize: '0.7rem',
  },
  '@media (min-width: 1680px)': {
    subHeader: {
      padding: '2rem 30rem 0 30rem',
    },
  },
  '@media (max-width: 1680px)': {
    subHeader: {
      padding: '2rem 30rem 0 30rem',
    },
  },
  '@media (max-width: 1280px)': {
    subHeader: {
      padding: '2rem 15rem 0 15rem',
    },
  },
  '@media (max-width: 1080px)': {
    subHeader: {
      padding: '2rem 20rem 0 20rem',
    },
  },
  '@media (max-width: 880px)': {
    subHeader: {
      padding: '2rem 5rem 0 5rem',
    },
  },
  '@media (max-width: 650px)': {},
  '@media (max-width: 500px)': {
    subHeader: {
      padding: '2rem 2rem 0 2rem',
    },
    hoursRow: {
      display: 'flex',
      justifyContent: 'space-between',
      flexDirection: 'row',
      '& span': {
        fontSize: '1rem',
        padding: ' 0 5% 0 5%',
        alignSelf: 'center',
        display: 'none',
      },
    },
  },
}));

export default useStyleProfile;
